body, html {
  min-height: 100%;
  @include iota-breakpoint(mobile) {
    position: relative;
    overflow: hidden;
  }
}

html {
  height: 100%;
  position: relative;
}

body {
  padding-top: 4.5rem;
  padding-bottom: 4.5rem;
  font-family: "ff-tisa-sans-web-pro", sans-serif;
  font-size: 1.8rem;
  line-height: 2.6rem;
  -webkit-font-smoothing: antialiased;
  overflow: hidden;

  @include iota-breakpoint(mobile) {
    padding-top: 3rem;
    padding-bottom: 3rem;
  }
}

* {
  outline: none!important;
}

a {
  text-decoration: none;
}

#rkwDigiKit {
  opacity: 0;
  transition: opacity 0.5s 0.8s ease-in;
  &.start {
    opacity: 1;
  }
  @include iota-breakpoint(mobile) {

    > .o-container {
      padding-top: 4.5rem;
    }

  }
}

.o-container {
  //border: 1px solid #e64415;
  position: relative;
}

.logo {
  @include iota-breakpoint(mobile) {
    left: 7.5rem;
  }
  position: absolute;
  width: 7rem;
  height: 7rem;
  top: 0;
  left: -3rem;
  transform: translatex(-100%);
  background: url($path-assets-images + 'rkw-logo.svg') no-repeat;
  background-size: cover;
}

.bookmarks {
  @include iota-breakpoint(mobile) {
    right: 7.5rem;
  }
  position: absolute;
  width: 5rem;
  height: 5rem;
  top: 0;
  right: -5rem;
  transform: translatex(100%);
  background: url($path-assets-images + 'folder-heart.svg') no-repeat center center;
  background-size: cover;
  &::after {
    position: absolute;
    top: 50%;
    left: 50%;
    font: normal normal normal 5rem/5rem 'rkw-digikit' !important;
    //content: "\e907";
    transform: translate3d(-50%, -50%, 0);
    color: #575757;
  }
}

.c-rkw-tutorial {
  @include iota-breakpoint(mobile) {
    top: .5rem;
    right: 16rem;
  }
  position: absolute;
  width: 5rem;
  height: 5rem;
  top: 7rem;
  right: -5rem;
  transform: translatex(100%);
  background: transparent;
  &::after {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    text-align: center;
    font: normal normal normal 3rem/3rem 'rkw-digikit' !important;
    content: "\e913";
    transform: translate3d(-50%, -50%, 0);
    color: #575757;
  }
}

.c-rkw-navigation-wrapper {
  position: relative;
  @include iota-breakpoint(mobile) {
    transition: transform 0.4s ease;
    &--open-left {
      transform: translate(17%, 0)
    }

    &--open-right {
      transform: translate(-17%, 0)
    }
  }
}

.c-rkw-customer {
  position: absolute;
  top: 50%;
  left: 50%;
  right: auto;
  width: 10rem;
  height: 10rem;
  margin-left: 0;
  transform: translate3d(-50%,-50%,0);
  transform-origin: center center;
  z-index: 750;
  background-color: #ffffff;
  font-family: "ff-tisa-sans-web-pro",sans-serif;
  color: #575757;
  @include iota-type(2.4rem);
  line-height: 10rem;
  text-align: center;
  cursor: pointer;
}


.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}

.whooshElement {
  position: fixed!important;
  opacity: 1;
  transition: opacity 1s ease, left 1s ease, top 1s ease;
  z-index: 8000;
  color: #ffffff;
}

.c-rkw-tooltip {
  position: fixed;
  z-index: 6000;
  display: inline-block;
  line-height: 2.4rem;
  width: auto;
  min-width: 7.5rem;
  max-width: 20rem;
  background: #575757;
  color: #ffffff;
  opacity: 0;
  transition: opacity 0.2s ease-in;
  padding: 1rem 1rem;
  box-shadow: 0.3rem 0.3rem 0.5rem rgba(51, 51, 51, 0.65);
  transform: translate3d(-100%, -100%, 0);

  &--show {
    opacity: 1;
  }

  &--right {
    transform: none;
  }

  &--color-1 {
    @extend .u-bg-rkw-color-1;
  }
  &--color-2 {
    @extend .u-bg-rkw-color-2;
  }
  &--color-3 {
    @extend .u-bg-rkw-color-3;
  }
  &--color-4 {
    @extend .u-bg-rkw-color-4;
  }
}


