// SelectBox




// Options

$bm-components-rkw-selectbox : true;

$bm-components-rkw-selectbox-namespace : 'rkw-selectbox' !default;

// Helper Local Variables

$bm-components-rkw-selectbox-namespace-var : $iota-global-components-namespace + $bm-components-rkw-selectbox-namespace;

$rkw-selectbox-selector: $bm-components-rkw-selectbox-namespace-var;



.#{$rkw-selectbox-selector} {
  position: relative;
}

.#{$rkw-selectbox-selector}__selected {
  position: relative;
  width: 100%;
  height: 4rem;
  border: .1rem solid #575757;
  background: #ffffff;
  line-height: 3.8rem;
  padding-left: 1rem;
  cursor: pointer;

  &::before {
    position: absolute;
    top: 0;
    right: 4rem;
    width: .1rem;
    height: 3.9rem;
    border-left: .1rem solid #575757;
    content: " ";
  }

  &::after {
    position: absolute;
    top: 1rem;
    right: 0;
    width: 4rem;
    height: 4rem;
    font: normal normal normal 2rem/4rem 'rkw-digikit' !important;
    content: "\e901";
    transform: rotate(90deg);
  }

}

.#{$rkw-selectbox-selector}__options {
  width: 100%;
  height: 0;
  border: none;
  background: #ffffff;
  padding-left: 0;
  overflow: hidden;
  position: absolute;
  top: 3.9rem;
  transition: height 0.1s ease-in-out;

  &--open {
    height: 30rem;
    border: .1rem solid #575757;
    overflow: auto;
  }

}

.#{$rkw-selectbox-selector}__option {
  padding-left: 1rem;
  cursor: pointer;
  &--active {
    background-color: #f4f4f4;
  }
  &:hover {
    background-color: #f4f4f4;
  }
}


