// Bookmark Slider




// Options

$bm-components-rkw-content-slider : true;

$bm-components-rkw-content-slider-namespace : 'rkw-content-slider' !default;

// Helper Local Variables

$bm-components-rkw-content-slider-namespace-var : $iota-global-components-namespace + $bm-components-rkw-content-slider-namespace;

$rkw-content-slider-selector: $bm-components-rkw-content-slider-namespace-var;

.#{$rkw-content-slider-selector} {

}

.#{$rkw-content-slider-selector}__item {
  &:hover {
    .#{$rkw-content-slider-selector}__text {
      transform: translate3d(-50%, -50%, 0)!important;
    }
  }
}

.#{$rkw-content-slider-selector}__text {
  width: 100%;
  height: auto;
  text-align: center;
  position: absolute;
  padding: 1.5rem;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0)!important;
  z-index: 200;
  color: #ffffff;
  font-size: 1.6rem;
  line-height: 2.1rem;
}

.#{$rkw-content-slider-selector}__next-item {
  display: block;
  position: relative;
  height: 100%;
  top: 0;
  right: 2rem;
  color: #ffffff;
  z-index: 100;
  outline: none!important;
  cursor: pointer;
  &::after {
    position: absolute;
    bottom: auto;
    top: 50%;
    right: 0;
    font: normal normal normal 4rem/4rem 'rkw-digikit' !important;
    content: "\e901";
    color: #ffffff;
    transform: translatey(-50%);
  }

  @include iota-breakpoint(mobile) {
    right: -2rem;
  }
}

.#{$rkw-content-slider-selector}__prev-item {
  display: block;
  position: relative;
  height: 100%;
  top: 0;
  left: 2rem;
  color: #ffffff;
  z-index: 100;
  outline: none!important;
  cursor: pointer;
  &::after {
    position: absolute;
    bottom: auto;
    top: 50%;
    left: 0;
    font: normal normal normal 4rem/4rem 'rkw-digikit' !important;
    content: "\e900";
    color: #ffffff;
    transform: translatey(-50%);
  }

  @include iota-breakpoint(mobile) {
    left: -2rem;
  }

}

.#{$rkw-content-slider-selector}__container {
  position: relative;
}

.#{$rkw-content-slider-selector}__slide {
  transition: opacity .5s ease-in-out;

  &-enter-active {
    opacity: 0;
    &:first-of-type {
      transition: margin-left .5s ease-in-out, opacity 0.5s ease-in-out;
    }
    &:last-of-type {
      transition: right .5s ease-in-out, opacity 0.5s ease-in-out;
      position: absolute;
    }

  }
  &-leave-active {
    opacity: 1;
    &:first-of-type {
      transition: margin-left .5s ease-in-out, opacity 0.5s ease-in-out;
    }
    &:last-of-type {
      transition: right .5s ease-in-out, opacity 0.5s ease-in-out;
      position: absolute;
    }
  }
}

.#{$rkw-content-slider-selector}__slide {

  &-enter {
    &:first-of-type {
      margin-left: -25%;
      opacity: 0;
    }
    &:last-of-type {
      right: -25%;
      opacity: 0;
    }

    &-to {
      &:first-of-type {
        margin-left: 0;
        opacity: 1;
      }
      &:last-of-type {
        right: 0;
        opacity: 1;
      }
    }
  }
  &-leave {
    &:first-of-type {
      margin-left: 0;
      opacity: 1;
    }
    &:last-of-type {
      right: 0;
      opacity: 1;
    }
    &-to {
      &:first-of-type{
        margin-left: -25%;
        opacity: 0;
      }
      &:last-of-type {
        right: -25%;
        opacity: 0;
      }
    }
  }
}


